import React from 'react'
//import '../App.scss';
import {
    BrowserRouter,
    Routes, //replaces "Switch" used till v5
    Route,
    HashRouter
} from "react-router-dom";

import Video from './Video';
import App from './App';

export default function Main() {
    return (
        <div className="App">
            <HashRouter>
                <Routes>

                    <Route path="/" element={<App />} />
                    <Route path="/video" element={<Video />} />

                </Routes>
            </HashRouter>
        </div>
    );
}