import React, { useState, useEffect } from 'react'
import logo from './logo.jpg';
import './App.css';
import Dropdown from 'react-dropdown';
import 'react-dropdown/style.css';
import { useNavigate, useLocation } from 'react-router-dom';

import { surah_wise_list } from './surah_wise_list';

const options = [
  'one', 'two', 'three'
];
const defaultOption = options[0];


function App() {

  const navigate = useNavigate();

  const [surah, setSurah] = useState('Al-Fatiha')
  const [myverse, setMyVerse] = useState('Al-Fatiha')

  const [surah_list, setSurahList] = useState([])
  const [indx, setIndx] = useState(-1);
  const [verse_list, setVerseList] = useState([])

  function fill_surah() {
    let temp_arr = [];
    for (let i = 0; i < surah_wise_list.length; i++) {
      temp_arr.push(surah_wise_list[i].ar);
    }
    setSurahList([...temp_arr])
  }

  function fill_verse(id) {
    console.log(id);
    let i = 1;
    let temp_arr = []
    let count = surah_wise_list[id].tot
    for (i = 1; i <= count; i++) {
      temp_arr.push(String(i))
    }
    setVerseList([...temp_arr]);
    setIndx(id)
  }

  const [width, setWidth] = useState(window.innerWidth);
  const [height, setHeight] = useState(window.innerHeight);
 

  const speed_select = (val) => {
    console.log(val.value);
    let nm = val.value;
    let id = 0;
    for (let i = 0; i < surah_list.list; i++) {
      if (surah_list[i] == nm) {
        id = i;
        break;
      }
    }
    setSurah(val.value);
    fill_verse(id)
  }

  useEffect(() => {
    if (indx == -1) {
      fill_verse(0)
    }
  });

  useEffect(() => {
    if (surah_list.length == 0) {
      fill_surah();
    }
  });

  return (
    <div className="App">
      <header className="App-header">
        <img src={logo} style={{ height: height / 6, width: width / 5, borderRadius: 10 }} alt="logo" />
        <text style={{ marginTop: 10, fontWeight: '700', letterSpacing: 1.03, fontStyle: 'italic' }}>Learn Quran 24/7</text>
        <p>
          <text style={{ letterSpacing: 1.02 }}>Select Surah From below</text>
        </p>
        <div style={{ display: 'flex', justifyContent: 'center', width: width / 4 }}>
          <Dropdown
            placeholder="Select surah"
            className="my-className"
            options={surah_list}
            onSelect={(value) => speed_select(value)} // always fires once a selection happens even if there is no change
          />
        </div>
        <p>
          <text style={{ letterSpacing: 1.02 }}>Select Verse No. From below</text>
        </p>
        <div style={{ display: 'flex', alignItems: 'center', justifyContent: 'space-between', width: width / 4, flexDirection: 'row' }}>
          <input type="text" value={indx} onChange={(event) => setIndx(event.target.value)} placeholder="Write your verse no." style={{ backgroundColor: 'white', outlineWidth: 0, outlineColor: 'transparent', outline: 'none', height: 30, borderTopRightRadius: 2, borderBottomRightRadius: 2, marginLeft: 1, borderStyle: 'solid', borderWidth: 1, borderColor: 'forestgreen', borderRadius: 8, padding: 4 }} />
          <Dropdown
            placeholder="Verse"
            className="my-className"
            options={verse_list}
          //onSelect={(value) => speed_select(value)} // always fires once a selection happens even if there is no change
          />
        </div>
        <div onClick={() => navigate('/video')} style={{ cursor: 'pointer', borderRadius: 10, marginTop: 50, width: 120, alignSelf: 'center', backgroundColor: 'forestgreen', padding: 4, justifyContent: 'center' }}>
          <text style={{ color: 'white', fontWeight: '700', letterSpacing: 1.02 }}>View ➤</text>
        </div>
        <div style={{ height: 10, width: width }} />
      </header>
    </div>
  );
}

export default App;
